.pageHeader{
    position: absolute;
    left: 50%;
    transform: translateX(-50%);
    top: 15px;
      padding: 0 25px;
      text-align: center;
      width: calc(100% - 40px);
      max-width: 1200px;
      height: 85px;
  
  
    
      border-radius: 14px;
      box-shadow: 0px 15px 30px 0px rgba(0,0,0,0.1);
  
      background:#ecf5f5;
      border: 1px solid #d8e2e2;
      border-bottom: 6px solid #d8e2e2;
      background:#fafafa;
      border: 1px solid   #d5d7db;
      border-bottom: 6px solid #d5d7db;
  }
  
  .pageHeaderWrapper{
      height: 85px;
      position: relative;
      width: 100%;
      xmax-width: 1440px;
  }
  
  .pageHeaderLogo{
    cursor: pointer;
    user-select: none;
      position: absolute;
      left: 0;
      top: 9px;
      width: 60px;
      height: 60px;
      background-image: url(./images/logo.png);
      background-size: contain;
      background-position: center;
      background-repeat: no-repeat;
  }
  
  .pageHeaderText{
    cursor: pointer;
    user-select: none;
    font-weight: 500;
    font-size: 24px;
    position: absolute;
    left: 70px;
    top: 0px;
    height: 85px;
    line-height: 78px;
  }
  
  .pageHeaderButton{
      padding: 2px;
      cursor: pointer;
      user-select: none;
      position: absolute;
      right: 142px;
      top: 15px;
      letter-spacing: 0.2px;
      font-size: 14px;
      font-weight: 500;
      border-radius: 10px;
      height: 50px;
     
      line-height: 40px;
      background: #63bebe;
      border: 1px solid #48a0a0;
      border-bottom: 6px solid #48a0a0;
  
  
      box-shadow: 0px 6px 16px 0px rgba(0,0,0,0.3);
    }
  
    .pageHeaderButton2{
      width: 132px;
      right: 0px;
      background: #865ce9;
      border: 1px solid #6234cc;
      border-bottom: 6px solid #6234cc;
    }
  
 
  
    .pageHeaderButtonInner{
        font-weight: 500;
        border-radius: 30px;
      padding: 0 14px;
        width: 100%;
        height: 100%;
       
  
        color: white;
        font-family: 'Roboto', sans-serif;
       
    }
  
    .pageHeaderButtonInner2{
      font-weight: 500;
        border-radius: 30px;
      padding: 0 14px;
        width: 100%;
        height: 100%;
       
  
        color: rgba(255,255,255,1);
        font-family: 'Roboto', sans-serif;
    }

    .connectOverlay{
      z-index: 99999;
      background-color: rgba(255,255,255,0.95);
      position: fixed;
      width: 100%;
      height: 100vh;
      left: 0;
      top: 0;
    }


    .connectWindow{
      padding: 30px;
      text-align: center;
      border-radius: 12px;
      position: absolute;
      width: 95%;
      max-width: 480px;
      left: 50%;
      top: 50%;
      transform: translateX(-50%) translateY(-50%);
      background: #fafafa;
      border: 1px solid #d5d7db;
      border-bottom: 6px solid #d5d7db;
  
    }

    .connectWindowHeader{
      cursor: pointer;
    -webkit-user-select: none;
    user-select: none;
    margin-bottom: 24px;
    border-radius: 20px;
    padding: 5px 14px;
    letter-spacing: 0.5px;
    font-size: 12px;
    color: white;
    font-weight: 500;
    background-color: black;
    color: white;
    font-family: 'Roboto', sans-serif;
    }

    .connectWindowClose{
      user-select: none;
      cursor: pointer;
      position: absolute;
      right: 30px;
      top: 30px;
      font-size: 21px;
    }

    .connectSpacer{
      width: 100%;
    }

    .connectWindowItem{
      user-select: none;
      cursor: pointer;
      border-radius: 12px;
      width: 120px;
      height: 120px;
      border: 1px solid #d5d7db;
      border-bottom: 3px solid #d5d7db;
    }

    .connectWindowItemIcon{
      width: 100%;
      height: 75px;
      margin-top: 8px;
      background-image: url(./images/meta.png);
      background-size: contain;
      background-position: center;
      background-repeat: no-repeat;
    }

    .connectWindowItemTitle{
      letter-spacing: 0.1px;
      width: 100%;
      font-family: 'Roboto', sans-serif;
      font-size: 14px;
      font-weight: 500;
    }