.faq{
    width: 100%;

   }
   
   .faqQ{
     cursor: pointer;
     user-select: none;
     min-height: 60px;
     line-height: 60px;
     text-transform: uppercase;
     width: 100%;
     text-align: left;
     font-size: 18px;
     letter-spacing: 0.2px;
     color: #5028af;
     font-weight: 600;
   }
   
   .faqQButton{
     user-select: none;
     width: 20px;
     height: 60px;
     float: right;
     font-size: 34px;
     line-height: 60px;
     font-weight: 200;
   }
   
   .faqA{
     width: calc(100% - 30px);
     padding-bottom: 14px;
     text-align: left;
     font-size:16px;
     line-height: 24px;
     font-family: 'Roboto', sans-serif;
     font-weight: 600;
     color: rgba(0, 0, 0, 0.75);
   }
   
   .spacer{
     margin-bottom: 30px;
   }
   
 
   
   .faqItem{
     padding: 0 10px;
     text-align: left;
     transition: 0.2s;
     max-height: 60px;
     overflow: hidden;
     width: 100%;
     border-bottom: 2px solid rgba(0, 0, 0, 0.1);
   }
   
   
   