



@media only screen and (min-width: 0px) and (max-width: 1010px){
    .walletBalance{
        width: 100%;
        margin-bottom: 26px;
    }
    .progressBar{
        height: auto;
        padding: 20px;
        margin:0;
        width: 100%;
        margin-bottom: 30px;
    }
    .progressBarItem{
        padding: 0 10px;
        text-align: center;
        height: auto;
        border: none;
        font-weight: 500;
        font-size: 12px;
        line-height: 17px;
        vertical-align: top;
    }
    .progressBarItemNumber{
        font-size: 20px;
        margin: 0;
        margin-bottom: 10px;
        width: 100%;
        background: none;
        color: black;
        border: none;
       
    }
    .progress{
        margin-top: 14px;
    }
    .aroma{
        margin-right: 30px;
        width: calc(50% - 15px);
    }
    .aroma2{
        margin-right: 0;
    }
    .saleItem{
        margin-top: 24px;
        width: 100%;
    }
}


@media only screen and (min-width: 0px) and (max-width: 680px){
    .aroma{
        margin-right: 0;
        width: 100%;
    }
    .aroma2{
        margin-top: 24px;
        margin-right: 0;
    }
    .saleItem{
        margin-top: 24px;
        width: 100%;
    }
    .saleItemHidden{
     
    }
}


@media only screen and (min-width: 0px) and (max-width: 500px){
    .faqQ{
        text-transform: capitalize;
        font-size: 14px;
    }
}


@media only screen and (min-width: 0px) and (max-width: 1190px){
  

    

    .section4Item{
        margin-bottom: 20px;
    }
    .section1RightWrapper{
        background-image: none;
    }
}


@media only screen and (min-width: 0px) and (max-width: 660px){

    .section1{
        height: 600px;
        min-height: 600px;
    }

    .section1Wrapper{
        margin-top: 35px;
    }

    .section1Button{
        bottom: 5%;
    }



    .section2Chef{
        max-height: 300px;
        max-width: 300px;
        background-position: 150px;
    }
    .section2Title2{
        font-size: 45px;
    }
    .pageHeaderText2{
        transform: scale(0.7) translateX(-56px);
    }
    .pageHeaderLogo{
        transform: scale(0.9) translateX(-10px);
    }
    .headerButtonWrapper{
        transform: scale(0.8) translateX(36px) translateY(1px);
    }
   
}


@media only screen and (min-width: 0px) and (max-width: 475px){
    .section2Chef{
        top: -30px;
    }

    .pageHeaderText2{
        display: none;
    }
    .pageHeaderHome{
        border-top-left-radius: 0;
        border-top-right-radius: 0;
        left: 1%;
        top: -1px;
        width: 98%;
        transform: none;
    }
}